<template>
    <div v-if=" this.group == 'AT212'" class="scheduleContainer">
      <!-- Раздел для сегодняшнего расписания -->
      <div class="scheduleElement" v-if="this.todaysSchedule.today">
        <h2>Розклад на сьогодні:</h2>
        <ul>
          <li v-for="lesson in this.todaysSchedule.today" :key="lesson.name">      
            <a v-if="lesson.link != null" :href="lesson.link">{{ lesson.name }}</a>
            <a v-else>{{ lesson.name }}</a>
          </li>
        </ul>
      </div>
      <div v-else>
        <p>Розклад на сьогодні не знайдено.</p>
      </div>
      <!-- Раздел для расписания на завтра -->
      <div class="scheduleElement" v-if="this.todaysSchedule.tomorrow">
        <h2>Розклад на завтра:</h2>
        <ul>
          <li v-for="lesson in this.todaysSchedule.tomorrow" :key="lesson.name">      
            <a v-if="lesson.link != null" :href="lesson.link">{{ lesson.name }}</a>
            <a v-else>{{ lesson.name }}</a>
          </li>
        </ul>
      </div>
      <div v-else>
        <p>Розклад на завтра не знайдено.</p>
      </div>
    </div>
    <div v-else>
      <!-- Add content for other groups if needed -->
    </div>
</template>

<script>

export default {
  data() {
    return {};
  },
  props: ['group', 'thisWeekSteamy','unpairWeekScheduleAT212','steamyWeekScheduleAT212'],
 
  mounted(){
 
  },

  methods: {
    
  },
  computed: {
  todaysSchedule() {
    const daysOfWeek = ["Неділя", "Понеділок", "Вівторок", "Середа", "Четвер", "П`ятниця", "Суббота"];
    const now = new Date();
    const dayOfWeek = now.getDay();

    const schedule = { today: [], tomorrow: [] };

    if (this.group === 'AT212') {
      const weekSchedule = this.thisWeekSteamy ? this.steamyWeekScheduleAT212 : this.unpairWeekScheduleAT212;

      schedule.today = weekSchedule[daysOfWeek[dayOfWeek]] ?? [];
      schedule.tomorrow = weekSchedule[daysOfWeek[dayOfWeek + 1]] ?? [];
    }

    return schedule;
  },
}
};
</script>